.content {
  display: flex;
}

.main-body {
  min-height: calc(100vh - 60px);
  display: flex;
  max-width: 1200px;
  flex-shrink: 0;
  margin: 0 1px;
}

@media (max-width: 1366px) {
  .main-body {
    width: 100%;
    margin: 0 auto;
  }
}

@media (max-width: 768px) {
  .main-body {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
}