.hidden-right-div {
  overflow: hidden;
  width: 50%;
  height: calc(100vh - 60px);
  background: url(../../icons/JS.png) no-repeat 50% 50% ;
  background-size: 100%;
}

.sidediv-right {
  width: 100%;
  height: 100%;
  background: url(../../icons/doors.png) no-repeat 50% 50%;
  background-color: var(--colors-bg);
}

.sidediv-right.active {
  transform: translateX(100%);
  transition: all 1600ms;
}

@media (max-width: 1366px) {
  .hidden-right-div {
    display: none;
  }
}